import React, { useEffect, useState } from 'react';
import Table from '../../../../components/Table';
import { useSelector } from 'react-redux';
import "./style.css";
import axiosInstance from "../../../../redux/axiosInstance";

const ClientDetails = () => {
  const [clients, setClients] = useState([]);
  const { user } = useSelector((state) => state.user);
  const [isFetching, setIsFetching] = useState(false);
  const columns = ['ClientName', 'MobileNumber', 'EmailId', 'Address', 'totalCost', 'CurrentBalance','RecordRegisteredDate'];
  const pageLimit = 10;

  useEffect(() => {
    getClients();
    // getTestName();
  }, []);

  const getClients = async () => {
    try {
      setIsFetching(true);
      const url = process.env.REACT_APP_API_URL + `/clients`;
      const { data } = await axiosInstance.get(url);
      setClients(data.data);
      setIsFetching(false);
    } catch (error) {
      console.log(error);
      setIsFetching(false);
    }
  };

  console.log(
    clients
  )
  const transformData = () => {
    return clients
      .map((client) => {
  
        return {
          ClientName: client.name,
          MobileNumber: client.mobileNo,
          EmailId: client.emailId,
          Address: client.address,
          totalCost:client.totalCost ?? 0,
          CurrentBalance:client.currentBalance ?? 0,
          RecordRegisteredDate: client.dateOfCreation,
        };
      });
  };
  
 
  
  
  
  

  return (
    <div className="container">
      <h1 style={{ color: "white", fontWeight: "600", marginBottom: "30px" }}>
        Details of Clients
      </h1>
      <Table data={transformData()} columns={columns} pageLimit={pageLimit} />
    </div>
  );
};

export default ClientDetails;
