const ClientDashboard = () => {

  
 

  return (
    <div className="dashboard_container">
      <Navbar />

      <div className="client_cont">
        <div>
          <SidebarAdmin />
        </div>
        <div className="client_area">
            <AdminRoutes/>
        </div>
      </div>
    </div>
  );
}

export default ClientDashboard